import React, {Component, useEffect} from "react";
import "./contact.styles.scss";
import CustomButton from "../common/custom-button/custom-button.component";
import {setTitle} from "../../util/utils";
import emailjs from 'emailjs-com';
const nodemailer = require('nodemailer');

export default class Contact extends Component
{
    constructor(props) {
        super(props);
        this.state = {
            from_email: "",
            html_message: ""
        };
    }
    componentDidMount() {
        setTitle("Contact");
    }
    setField = (event) => {
        const {name, value} = event.target;
        this.setState({[name]: value});
    };
    sendEmail = (event) => {
        event.preventDefault();
        console.log(this.state);
        emailjs.sendForm('gmail', 'contact_us', event.target, 'user_PgPU3Y0ZVne4jIcFILgw5')
            .then((result) => {
                window.location.reload()  //This is if you still want the page to reload (since e.preventDefault() cancelled that behavior)
            }, (error) => {
                console.log(error.text);
            });
        /*
        RNSmtpMailer.sendMail({
            mailhost: "smtp.gmail.com",
            port: "465",
            ssl: true, //if ssl: false, TLS is enabled,**note:** in iOS TLS/SSL is determined automatically, so either true or false is the same
            username: "usernameEmail",
            password: "password",
            from: "fromEmail",
            recipients: "toEmail1,toEmail2",
            bcc: ["bccEmail1", "bccEmail2"], //completely optional
            subject: "subject",
            htmlBody: "<h1>header</h1><p>body</p>",
            })
            .then(success => console.log(success))
            .catch(err => console.log(err));
        */
    };
    render() {
        return (
            <div className="contact-component">
                <form onSubmit={this.sendEmail}>
                    <h1>Contact Us</h1>
                    <div className="title">
                        Email:&nbsp;<span className="mandatory">*</span>
                    </div>
                    <input value={this.state.from_email} className="input-field" type="text" size="100"
                           name="from_email"
                           onChange={this.setField} />
                    <div className="title">
                        Message:&nbsp;<span className="mandatory">*</span>
                    </div>
                    <textarea value={this.state.html_message} className="input-field" rows="4" cols="100"
                              name="html_message"
                              onChange={this.setField} />
                    <div className="submit">
                        <CustomButton onClick={this.onSubmit}>Submit</CustomButton>
                    </div>
                </form>
            </div>
        )
    }
}
