import React from "react";
import "./categories-stripe.styles.scss";
import categories from "../../data/categories";

export default function CategoriesStripe() {
    return (
        <div className="categories-stripe-component">
            {categories.map(category => {
                return (
                    <div key={category.code} className="category-item">
                        <img src={category.imageUrl} alt="category image" />
                        <div className="name">{category.displayName}</div>
                    </div>
                )
            })}
        </div>
    )
}